@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

@mixin inter-font {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings:
    "slnt" 0;
}

$color-extra-dark: #1D1D2D;
$color-white: #F2F2F2;
$color-dark: #252534;
$color-primary: #a4cc71;
$color-primary-light-tint: #b7e37e;
$color-secondary: #9CDCFE;
$color-secondary-light-tint: #b7e5fe;
$color-pink: #C586A1;
$color-pink-light-tint: #e7aec7;
$color-blue: #9CDCFE;
$color-blue-light-tint: #b5dbef;
$color-orange: #CE9178;
$color-orange-light-tint: #d7a997;

$padding-basic: 2rem;
$padding-button: 0.8rem 1.8rem;

@mixin uncolored-button() {
  padding: $padding-button;
  border-radius: 1rem;
  @include inter-font();
  color: $color-dark;
  font-size: 1rem;
  font-weight: 600;
  width: fit-content;
}

html {
  font-size: 16px;
  background-color: $color-extra-dark;
}

header {
  background-color: $color-extra-dark;
  padding: $padding-basic;
  border-bottom: 0.5px solid #f2f2f236;
  display: flex;
  gap: 1rem;

  h1 {
    color: $color-white;
    @include inter-font();
  }
}

input {
  background-color: $color-white;
  width: 30rem;
  padding: $padding-button;
  font-size: 1.5rem;
  border-radius: 1rem;
}

textarea {
  background-color: $color-white;
  padding: $padding-button;
  font-size: 1.5rem;
  width: 45rem;
  height: 24rem;
  border-radius: 1rem;
}

h3 {
  @include inter-font();
  color: $color-white;
}

button {
  background-color: $color-primary;
  padding: $padding-button;
  border-radius: 1rem;
  @include inter-font();
  color: $color-dark;
  font-size: 1rem;
  font-weight: 600;
  width: fit-content;

  &:hover {
    background-color: $color-primary-light-tint;
  }

  &:disabled {
    background-color: grey;
    cursor: not-allowed;
  }
}

#replacement-text-section {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.replacement-text-controls {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

#main-section {
  background-color: $color-extra-dark;
  padding: $padding-basic;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 1.5rem;

  h1 {
    color: $color-white;
    @include inter-font();
    font-size: 1.6rem;
  }
}

#yaml-output-section {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

#yaml-output-section-controls {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.pink-button {
  @include uncolored-button();
  background-color: $color-pink;
  &:hover {
    background-color: $color-pink-light-tint;
  }
}

.blue-button {
  @include uncolored-button();
  background-color: $color-blue;
  &:hover {
    background-color: $color-blue-light-tint;
  }
}

.orange-button {
  @include uncolored-button();
  background-color: $color-orange;
  &:hover {
    background-color: $color-orange-light-tint;
  }
}